.sidebar {
	background: #fff;
	height: 100vh;
	margin-top: -65px;
}

.search {
	position: relative;
	margin: 15px 0 8px 0;
	padding: 0 15px;
}

.input-search {
	width: 100%;
	height: 40px;
	padding: 0 15px;
	border: 0;
	border-radius: 8px;
	box-shadow: 0 0 8px 1px rgba(0,0,0,0.1);
}

.icon-magnifier {
	position: absolute;
	top: 12px;
	right: 30px;
	background: url('../../source/search.svg') no-repeat center/contain;
	display: block;
	width: 18px;
	height: 18px;
}

.country_list {
	height: calc(100vh - 75px);
}

.item {
	color: #323232;
	display: flex;
	justify-content: space-between;
	padding: 16px 25px;
	border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.item:hover{
	transform: translateY(-3px);
	box-shadow: 2px 2px 5px rgba(154, 147, 140, 0.5), 1px 1px 5px rgba(255, 255, 255, 1);
	cursor: pointer;
	text-shadow: 
		-0   -2px 0   #FFFFFF,
		 0   -2px 0   #FFFFFF,
		-0    2px 0   #FFFFFF,
		 0    2px 0   #FFFFFF,
		-2px -0   0   #FFFFFF,
		 2px -0   0   #FFFFFF,
		-2px  0   0   #FFFFFF,
		 2px  0   0   #FFFFFF,
		-1px -2px 0   #FFFFFF,
		 1px -2px 0   #FFFFFF,
		-1px  2px 0   #FFFFFF,
		 1px  2px 0   #FFFFFF,
		-2px -1px 0   #FFFFFF,
		 2px -1px 0   #FFFFFF,
		-2px  1px 0   #FFFFFF,
		 2px  1px 0   #FFFFFF,
		-2px -2px 0   #FFFFFF,
		 2px -2px 0   #FFFFFF,
		-2px  2px 0   #FFFFFF,
		 2px  2px 0   #FFFFFF,
		-2px -2px 0   #FFFFFF,
		 2px -2px 0   #FFFFFF,
		-2px  2px 0   #FFFFFF,
		 2px  2px 0   #FFFFFF;
	/* text-shadow: 2px 2px 0px #FFFFFF, 5px 4px 0px rgba(0,0,0,0.15); */
	transition: transform 0.2s ease-in;
}

.item:hover .icon-add{
	visibility: visible;
}

.item:active{
	box-shadow: inset 2px 2px 5px rgba(154, 147, 140, 0.5), 1px 1px 5px rgba(255, 255, 255, 1);
}

.item-country {
	transition: transform 0.15s ease-in;
}

.button-add {
	background: transparent;
	border: 0;
	cursor: pointer;
}

.button-add:hover .icon-add {
	opacity: 1;
}

.icon-add {
	opacity: 0.7;
	border-radius: 50%;
	visibility: hidden;
	background: url('../../source/plus.svg') no-repeat center/contain;
	background-color: #FFFFFF;
	display: block;
	width: 28px;
	height: 28px;
	transition: opacyti 0.15s ease-in;
	box-shadow: inset 0px -3px 1px rgba(0, 0, 0, 0.45), 0px 2px 2px rgba(0, 0, 0, 0.25);
}

.icon-add:active{
	box-shadow: inset 0px -3px 1px rgba(255, 255, 255, 1), inset 0 0px 3px rgba(0, 0, 0, 0.9);
}