  font-weight: 300;
  text-transform: uppercase;
  max-width: 100%;
  white-space: nowrap;
  vertical-align: middle;
  width: 175px;
  color: rgb(255, 255, 255);
  height: 45px;
  line-height: 45px;
  border-width: 0;
  text-decoration: none;
  border-radius: 30px;
  padding: 0 8px;
  -webkit-transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
