  outline: none;
}

.button-send:enabled {
  background: #41A6F1;
  cursor: pointer;
}

.button-send:disabled {
  background-color: #cecece;
  cursor: default;
}

.button-send:enabled.button-send:hover {
  background: #75c4ff;
}

.field-valid {
  position: absolute;
  top: calc(50% - (15px / 2));
  left: -25px;
